.mcq-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.question-container {
  margin-bottom: 20px;
}

.options-form {
  margin-top: 10px;
}

.option-item {
  margin-bottom: 10px;
}

.next-btn {
  display: block;
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.result-container {
  text-align: center;
  margin-top: 20px;
}
.button{
  display:flex;
  justify-content: space-between;
  
}